import React, { Component } from 'react'
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs'
import NApi from '../../Component/NumbersAPI/NApi';
import FAQ from '../../Component/CorporateBond/FAQ';
import SGBMain from '../Home/images/SGB/1.png'
import SGBF1 from '../Home/images/SGB/Interest Rates.svg'
import SGBF2 from '../Home/images/SGB/Investment.svg'
import SGBF3 from '../Home/images/SGB/Maturity.svg'
import SGBF4 from '../Home/images/SGB/Risk.svg'
import { withTranslation } from 'react-i18next';

export class SGB extends Component {
    data = {
        "SGB": {
            "Title": "",
            "ProductCategoryId": "7",
            "SebiCategory": "",
            "SebiSubCategory": "",
            "DefaultProductId": "1",
            "faqs": [
                {
                    "heading": "SGB:sGBF32",
                    "description": `SGB:sGBF33`
                },
                {
                    "heading": "SGB:sGBF38",
                    "description": `SGB:sGBF39`
                },
                {
                    "heading": "SGB:sGBF40",
                    "description": `SGB:sGBF41`
                },
                {
                    "heading": "SGB:sGBF42",
                    "description": `SGB:sGBF43`
                },
                {
                    "heading": "SGB:sGBF44",
                    "description": `SGB:sGBF45`
                }

            ]
        }

    }
    render() {

        const productinfo = this.data.SGB;
        const{t} = this.props;

        return (
            <>
                <div className='container container-wrapper'>
                    <div className='col-lg-7 col-md-6 mb-4'>
                        <h2>{t("SGB:sGBF1")}</h2>
                        <p>{t("SGB:sGBF2")}</p>
                        <a href="https://app.nivesh.com/home/index"><button type="button" className="btn btn-primary btn-lg">{t('CTAFooter:InvestNow1')}</button></a>
                    </div>
                    <div className='col-lg-5 col-md-6'>
                        <img src={SGBMain} alt="" className='img-fluid' />
                    </div>
                </div>


                <div className='bg-light'>
                    <div className='container container-wrapper'>
                        <h2 className='text-center'>{t("SGB:sGBF3")}</h2>
                        <div className='row'>
                            <div className='col-lg-6 mb-4'>
                                <div className='p-3 bg-white h-100'>
                                    <div className='col-lg-3'>
                                        <img src={SGBF1} alt="" className='img-fluid' />
                                    </div>
                                    <div className='col-lg-9'>
                                        <h3>{t("SGB:sGBF4")}</h3>
                                        <p className='small-text'>{t("SGB:sGBF5")}</p></div>
                                </div>
                            </div>
                            <div className='col-lg-6 mb-4'>
                                <div className='p-3 bg-white h-100'>
                                    <div className='col-lg-3'>
                                        <img src={SGBF2} alt="" className='img-fluid' />
                                    </div>
                                    <div className='col-lg-9'>
                                        <h3>{t("SGB:sGBF6")}</h3>
                                        <p className='small-text'>{t("SGB:sGBF7")}</p>
                                    </div>
                                </div>
                            </div>
                            <div className='col-lg-6 mb-4'>
                                <div className='p-3 bg-white h-100'>
                                    <div className='col-lg-3'>
                                        <img src={SGBF3} alt="" className='img-fluid' />
                                    </div>
                                    <div className='col-lg-9'>
                                        <h3>{t("SGB:sGBF8")}</h3>
                                        <p className='small-text'>{t("SGB:sGBF9")}</p>
                                    </div>
                                </div>
                            </div>
                            <div className='col-lg-6 mb-4'>
                                <div className='p-3 bg-white h-100'>
                                    <div className='col-lg-3'>
                                        <img src={SGBF4} alt="" className='img-fluid' />
                                    </div>
                                    <div className='col-lg-9'>
                                        <h3>{t("SGB:sGBF10")}</h3>
                                        <p className='small-text'>{t("SGB:sGBF11")}</p>
                                    </div>
                                </div>
                            </div>

                        </div>

                        <div className='text-center'><a href="https://app.nivesh.com/home/index" className='btn btn-primary'>{t('CTAFooter:InvestNow1')}</a></div>

                    </div>
                </div>
                <div className='gardient-red'>
                    <div className='container container-wrapper'>
                        <h2 className='text-white'>{t("SGB:sGBF12")}</h2>
                        <p className='text-white'>{t("SGB:sGBF13")}<br /><br />

                        {t("SGB:sGBF14")}<br /><br />

                        {t("SGB:sGBF15")}

                        </p>
                    </div>
                </div>
                <div className="container container-wrapper">
                    <h2>Investing in Sovereign Gold Bonds
                    </h2>
                    <Tabs>
                        <TabList>
                            <Tab>{t("SGB:sGBF16")}</Tab>
                            <Tab>{t("SGB:sGBF25")}</Tab>
                            <Tab>{t("SGB:sGBF27")}</Tab>

                        </TabList>
                        <TabPanel>
                            <ul>
                                <li><b>{t("SGB:sGBF17")}</b>: {t("SGB:sGBF18")}</li>
                                <li><b>{t("SGB:sGBF19")}</b>: {t("SGB:sGBF20")}</li>
                                <li><b>{t("SGB:sGBF21")}</b>: {t("SGB:sGBF22")}</li>
                                <li><b>{t("SGB:sGBF23")}</b>: {t("SGB:sGBF24")}</li>
                            </ul>

                        </TabPanel>
                        <TabPanel>
                            <p>{t("SGB:sGBF26")}</p>

                        </TabPanel>
                        <TabPanel>
                            <p>{t("SGB:sGBF28")}
                            </p>
                        </TabPanel>

                    </Tabs>
                </div>

                <NApi />

                <div className="container testimonials">
                    <div className="row">
                        <div className="col-lg-12">
                            <h2>{t("SGB:sGBF31")}</h2>

                            {productinfo && productinfo.faqs.map(faq =>
                                <FAQ heading={t(`${faq.heading}`)} description={t(`${faq.description}`)} />
                            )}

                        </div>
                    </div>
                </div>
            </>

        )
    }
}

export default withTranslation() (SGB)
